import axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://express-kuku.herokuapp.com/',
    // headers:{'KEY FOR CONNECTED WITH SERVER':"12312-12312-SDF-34SDF2"}
});
//_____________________________________________________________________________________
export const UserAppAPI = {

    exit() {
        return instance.get('exit').then(response => {
            return response.data
        })
    },
    registration(form) {
        return instance.post('registration', {
            Login: form.login,
            Password: form.password,
            Nickname: form.name
        }).then(response => {
            return response.data
        })
    }
};
//__________________________________________________________________________________
export const UserLoginAPI = {
    getUserFromServer() {
        debugger
        return instance.get('login').then(response => {
            debugger
            return response.data
        })
    },
    sendUserFormServer(value) {
        debugger
        return instance.post('login', {Login:value.login, Password:value.password}).then(response => {
            debugger
            return response.data
        })
    }
};
//___________________________________________________________________________________
export const UserProfitAPI = {

    getProfileFromServer(profile) {
        return instance.get(`profile/${profile}`).then(response => {
            return response.data
        })
    },

    sendInformation(inform){
        return instance.post(`update/info`, {inform})
    }

};
//_____________________________________________________________________________________
export const UserMessageAPI = {
    messageListFriend() {
        return instance.get(`message/friend`).then(response => {
            return response.data
        })
    },
    messageListParty() {
        return instance.get(`message/party`).then(response => {
            return response.data
        })
    },
    messageListMessage({id,list}) {
        debugger
        return instance.post(`message/list`,{id,list}).then(response => {
            debugger
            return response.data
        })
    },
    SendMessage(profile) {
        return instance.post(`message/send`, {profile}).then(response => {
            return response.data
        })
    },
    SendSetting(setting) {
        debugger
        return instance.post(`message/setting`, {setting}).then(response => {
            debugger
            return response.data
        })
    }
};
//_____________________________________________________________________________________
export const UserSubAPI = {
    getPeople({tags,nickname,list}) {
        return instance.post('people',{tags,nickname,list}).then(response => {
            return response.data
        })
    },
    subScribe(friend) {
        return instance.post('subscribe', {id: friend}).then(response => {
            return response.data
        })
    },
};
