import React from 'react'
import "./style.css"

export const Input = ({input, meta, value, ...props}) => {
    const error = meta.touched && meta.error;
    return (
        <div>
            <div>
                <input {...input} {...props} className={error && "formControl"}/>
            </div>
            {error && <span className={'formSpan'}>{meta.error}</span>}
        </div>
    )
}

export const Textarea = ({input, meta, ...props}) => {
    const errore = meta.touched && meta.error;
    return (
        <div>
            <div className={errore && "formControl"}>
                <textarea {...input} {...props} wrap="soft" rows={'1'}/>
            </div>
            {errore && <span>{errore}</span>}

        </div>
    )
}