import {UserMessageAPI} from "../../api/api";
import Cookies from "js-cookie";

let message = {
    Message:{ListMessage:[],AvaParty:'',NameParty:[]}
    ,Friend:[]
    ,Party:[]
};
//#####################################################################
const MessageReducer = (state = message, action) => {
    switch (action.type) {
//##############################################
        case "SET-FRIEND"  :
            return {
                ...state
                , Friend: action.friend.Friend
            };
        case "SET-PARTY"  :
            return {
                ...state
                , Party: action.friend.Party
            };
        case "SET-MESSAGE"  :
            return {
                ...state
                , Message: action.message
            };
//#################################################
        case "PUSH-MESSAGE" :
            return {
                ...state
                ,Message: {... state.Message, ListMessage:[...state.Message.ListMessage,{
                        user: action.message.user
                        , text: action.message.text
                        , img:action.message.img
                        ,date:action.message.date}
                        ]
                }
            };
        case "DELETED-MESSAGE" :
            for(let i=0;i<action.deleted.list.length;i++){
                let r=state.Party.findIndex(r=>r._id===action.deleted.list[i]);
                state.Party.splice(r,r+1)
            }
            return {
                ...state
            };
//#################################################
        default             :
            return state
    }
};
//#####################################################################
//Action for ComponentDid Mount or ComponentDidUpdate
export let SetMessage = (message) => ({type: "SET-MESSAGE", message});
export let SetFriend = (friend) => ({type: "SET-FRIEND", friend});
export let SetParty = (friend) => ({type: "SET-PARTY", friend});
//############################################################################
//Action for changing state
export const pushMessage = (message) => ({type: "PUSH-MESSAGE", message});
export const deleteParty = (deleted) => ({type: "DELETED-MESSAGE", deleted});
//############################################################################
export const GetListMessages = (profile) => async (dispatch) => {
    if(profile.id) {
        let data = await UserMessageAPI.messageListMessage(profile);
        if (data) {
            Cookies.set('message', profile);
            dispatch(SetMessage(data));
        }
    }
};//получить список сообщений
export const GetListFriend = () => async (dispatch) => {
    let data = await UserMessageAPI.messageListFriend();
    data && dispatch(SetFriend(data));
};//получить список друзей
export const GetListParty = () => async (dispatch) => {
    let data = await UserMessageAPI.messageListParty();
    data && dispatch(SetParty(data));
};//получить список друзей

export const SendMessage = (profile) => async (dispatch) => {
    let data = await UserMessageAPI.SendMessage(profile);
    data && dispatch(pushMessage(profile));
};//отправить сообщение
export const SendSetting = (setting) => async (dispatch) => {
    let data=await UserMessageAPI.SendSetting(setting);
    data && dispatch(deleteParty(setting))
};//отправить сообщение
//####################################################################################################
export default MessageReducer;
