import React, {PureComponent} from 'react';

import ReduxRegistrationForm from "./Registration";

import {compose} from "redux";
import {connect} from "react-redux";
import {RegistrationThunk} from "./RegistrationReducer";
import {RegistrationMessage, RegistrationStatus} from "../../user-selector/RegistrationSelector";
import {redirectInLogin} from "../../hoc/RegistrationOnLogin";


class RegistrationCont extends PureComponent {

    EntryForm = (formData) => {
        formData.name=formData.name.match(/(\w+)/gim);
        console.log(formData);
        this.props.RegistrationThunk(formData)
    };
    render() {
        return <div>
            <ReduxRegistrationForm onSubmit={this.EntryForm}
                                   userEntry={this.props.userEntry}
            />
        </div>
    }
}

const mapStateTo = (state) => ({
    message: RegistrationMessage(state)
    , status: RegistrationStatus(state)
});
export default compose(redirectInLogin, connect(mapStateTo, {RegistrationThunk}))(RegistrationCont)


