import React from 'react'
import {Redirect} from "react-router";
import {connect} from "react-redux";

const redirectInProfiles = (Component) => {

    let state = (state) => ({state: state.LoginReducer, userStatus: state.ProfileReducer.status});

    class ComponentRedirect extends React.Component {
        state={
        History :  localStorage.History
    };
        render() {
            if (this.props.state.status) {
                switch (History) {
                    case 'profile' :
                        return <Redirect to={`/profile/${this.state.Profile}`}/>;
                    case 'message' :
                        return <Redirect to={`/message/${this.state.Dialog}`}/>;
                    case 'people' :
                        return <Redirect to={`/people`}/>;
                    default  :
                        return <Redirect to={`/profile`}/>;
                }
            } else {
                return <Component {...this.props} />
            }
        }
    }
    return connect(state)(ComponentRedirect)
};
export default redirectInProfiles