import {UserSubAPI} from "../../api/api";

let people = {
    loading: false
    ,people:[]
};
//#####################################################################
const PeopleReducer = (state = people, action) => {
    switch (action.type) {
//##############################################
        case "GET-USERS"    :
            return {
                ...state
                , people: action.people.people
            };
        case "FOLLOW-PEOPLE"    :
            let f=state.people.filter(r=>r._id===action.follow);
                f[0].friendStatus? f[0].friendStatus=false:f[0].friendStatus=true;
            return {
                ...state
            };
        case 'LOADING'      :
            return {
                ...state
                , loading: action.loading
            };
        case 'CURRENT'      :
            return {
                ...state
                , current: action.current
            };
//#################################################
        default             :
            return state
    }
};
//#####################################################################
//Action for ComponentDid Mount or ComponentDidUpdate
export let SetPeople = (people) => ({type: "GET-USERS", people});
export let Loading = (loading) => ({type: 'LOADING', loading});
export let SetCurrent = (current) => ({type: 'CURRENT', current});
//############################################################################
//Action for changing state
export const followPeople = (follow) => ({type: "FOLLOW-PEOPLE", follow});
//####################################################################################################
export const GetUser = (page) => async (dispatch) => {
    let data = await UserSubAPI.getPeople(page);
    data && dispatch(SetPeople(data));
};
export const FollowPeople = (friend) => async (dispatch) => {
    let data = await UserSubAPI.subScribe(friend);
    data && dispatch(followPeople(friend));
};
//####################################################################################################
export default PeopleReducer;
