import React from 'react'
import {NavLink} from "react-router-dom";
import {compose} from "redux";
import {connect} from "react-redux";
import {Exit} from "../Content/Login/LoginReducer";
import './style/style.css';

let Menu = (props) => {
    function exit() {
        props.Exit()
    }

    return (
        <div className="Menu">
            <div><NavLink to='/profile' className="Button_item">Моя Страница </NavLink></div>
            <div><NavLink to="/message" className="Button_item">Сообщения </NavLink></div>
            <div><NavLink to='/people' className="Button_item">Пользователи </NavLink></div>
            <div><img src={'https://upload.wikimedia.org/wikipedia/commons/thumb/8/89/ISO_7010_E002_%2B_arrow_1.svg/240px-ISO_7010_E002_%2B_arrow_1.svg.png'}
                      onClick={exit}
                      className={'exit'}
            /></div>
        </div>
    )
}
const mapStateTo = (state) => ({state: state.LoginReducer,});
export default compose(
    connect(mapStateTo, {Exit}
    )
)(Menu)