import {UserProfitAPI} from "../../api/api";

let profileState = {
    status: false
    , message: 'Profile not entry'
    , loading: false


};
//#####################################################################
const ProfileReducer = (state = profileState, action) => {
    switch (action.type) {
//##############################################
        case "SET-PROFILE"  :
            return {
                ...state
                , status: action.profile.status
                , message: action.profile.mess

                , profileNickname: action.profile.Profile.Nickname
                , profilePhoto: action.profile.Profile.Photo.origin!==''
                                                        ?action.profile.Profile.Photo.origin
                                                        :action.profile.Profile.Photo.default
                , profilePhotoMin: action.profile.Profile.PhotoMin.origin!==''
                    ?action.profile.Profile.PhotoMin.origin
                    :action.profile.Profile.PhotoMin.default

                , profileStatus: action.profile.Profile.Status
                , profileLocation: action.profile.Profile.Location
                , profileAge: action.profile.Profile.Age
                , profileSkills: action.profile.Profile.Skills
                , profileTags: action.profile.Profile.Tags
                , profileGender: action.profile.Profile.Gender
            };
        case 'LOADING'      :
            return {
                ...state
                , loading: action.loading
            };
//#################################################
            case "EDIT-INFO"  :
            return {
                ...state
                , profileNickname: action.inform.nickname
                , profilePhoto: action.inform.photo
                , profileStatus: action.inform.status
                , profileLocation: action.inform.location
                , profileAge: action.inform.age
                , profileSkills: action.inform.skills
                , profileTags: action.inform.tags
                , profileGender: action.inform.gender
            };
//#################################################
        default             :
            return state
    }
};

//$$$$$$___Action for ComponentDid Mount or ComponentDidUpdate____$$$$$
export let SettingProfile = (profile) => ({type: "SET-PROFILE", profile});
export let Loading = (loading) => ({type: 'LOADING', loading});
//$$$$$$___Action for changing state____$$$$$
export const EditInform = (inform) => ({type: "EDIT-INFO", inform});
//####################################################################################################

//$$$$$$___Thunk for send on Server____$$$$$
export const GetProfileFromServer = (id) => async (dispatch) => {
        let data = await UserProfitAPI.getProfileFromServer(id);
        data && dispatch(SettingProfile(data));
};//поолучить пользователя

export const sendInformation = (inform) => async (dispatch) => {
        let data=await UserProfitAPI.sendInformation(inform);
        data && dispatch(EditInform(inform));
};//изменить статус

//####################################################################################################
export default ProfileReducer;
