import {UserAppAPI} from "../../api/api";

let status = {
    RegistrationStatus: false
    , RegistrationMessage: ''
}
//#####################################################################
// eslint-disable-next-line no-unused-vars
const RegistrationReducer = (state = status, action) => {
    if (action.type === 'REGISTRATION-STATUS') {
        return {
            ...state
            , RegistrationStatus: action.registration.status
            , RegistrationMessage: action.registration.mess
        }
    } else {
        return {
            ...state
        }
    }
};
//#####################################################################
//Action for ComponentDid Mount or ComponentDidUpdate
export let RegistrationDispatch = (registration) => ({type: 'REGISTRATION-STATUS', registration});
//############################################################################
//Thunk for send on Server
export const RegistrationThunk = (FormRegistration) => (dispatch) =>{
        UserAppAPI.registration(FormRegistration).then(data => {
            data.status && dispatch(RegistrationDispatch(data))
        })
};
//####################################################################################################
export default RegistrationReducer;
