import {UserAppAPI, UserLoginAPI} from "../../api/api";
import {stopSubmit} from 'redux-form'

let loginization = {
    status: false
    , message: String
    , user: Object
};
//#####################################################################
const LoginReducer = (state = loginization, action) => {
    switch (action.type) {
//##############################################
        case 'SET-LOGIN':
            return {
                ...state
                , status: action.user.status
                , message: action.user.message
                , user: action.user._id
            };
        case 'DEL-USER':
            return {
                ...state
                , status: false
                , message: String
                , user: Object
            };
//#################################################
        default             :
            return {
                ...state
            };
    }
};
//#####################################################################
export let SettingUserDispatch = (user) => ({type: 'SET-LOGIN', user});
export let DeletedUserDispatch = () => ({type: 'DEL-USER'});
//############################################################################
export const GetUserFromServer = () =>
    async (dispatch) => {
        let data = await UserLoginAPI.getUserFromServer();
    data ? dispatch(SettingUserDispatch(data))
         : dispatch(DeletedUserDispatch());
};//получить пользователя из куков
export const SendUserFormForServer = (formData) => async (dispatch) => {
    let data = await UserLoginAPI.sendUserFormServer(formData);
    data ? dispatch(SettingUserDispatch(data))
         : dispatch(stopSubmit('login', {_error: data.mess}))
};//войти с помощью пароля и логин
export const Exit = () => (dispatch) => {
    UserAppAPI.exit().then(data => {
        data && dispatch(DeletedUserDispatch());
    })
};//очистить стейт и удалить куки о пользователе
//####################################################################################################
export default LoginReducer;
