import React, {Suspense, lazy} from 'react'
import {Route} from "react-router";
import MenuHeader from "../Header/MenuHeader";
import {withAuthRedirect} from "../hoc/withAuthRedirect";
import LoAd from "../conponent/load";
import {getLoading, getLoginUser, getUserEntry} from "../user-selector/LoginSelector";
import {compose} from "redux";
import {connect} from "react-redux";

const ProfileCont = lazy(() => import ('./Profile/ProfileCont'));
const MessageCont = lazy(() => import("./Messages/MessageCont"));
const PeopleCont = lazy(() => import("./People/PeopleCont"));

let ContentCont = (props) => {
    return <div className={'cont_Section'}>
        {props.log && props.mess && props.peo && <div><LoAd/></div>}
        {!props.log && !props.mess && !props.peo && <div><MenuHeader/></div>}
        {!props.log && !props.mess && !props.peo && <div> <Content/></div>}
           </div>
};

const mapStateTo = (state) => ({
    log: state.LoginReducer.loading
    , mess:  state.MessageReducer.loading
    , peo:  state.PeopleReducer.loading
});

export default compose( connect(mapStateTo),withAuthRedirect )(ContentCont)


let Content = () => {
    return <Suspense fallback={'.... чу херня происходит, давай подождем'}>
        <div>
            <Route path='/profile/:profile?' render={() =><ProfileCont/>}/>
            <Route path='/message/:profile?' render={() => <MessageCont/>}/>
            <Route path='/people' render={() =><PeopleCont/>}/>
        </div>
    </Suspense>
};
