import {applyMiddleware, combineReducers, compose, createStore} from 'redux';
import {reducer as formReducer} from 'redux-form'
import thunkMiddleware from 'redux-thunk'

import LoginReducer from "./Content/Login/LoginReducer";
import RegistrationReducer from "./Content/Registration/RegistrationReducer";
import ProfileReducer from "./Content/Profile/ProfileReducer";
import MessageReducer from "./Content/Messages/MessageReducer";
import PeopleReducer from "./Content/People/PeopleReducer";


let reducers = combineReducers({
    LoginReducer: LoginReducer,
    RegistrationReducer: RegistrationReducer,

    ProfileReducer: ProfileReducer,
    MessageReducer: MessageReducer,
    PeopleReducer: PeopleReducer,

    form: formReducer,
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddleware))
);

window.store = store;

export default store;


