// клика он запоминает, при уходе он проверит заполнили ли вы его
import {compose} from "redux";
import {connect} from "react-redux";
import React from "react";


export const required = (value) => {
    if (value) return undefined
    return 'Field is required'
};

//Это устанавливает проверку на максимальную длину записанного
export const maxLengthCreator = (maxLength) => (value) => {
    return (
        value && value.length > maxLength
            ? `Max length is ${maxLength} symbols`
            : undefined
    )
};

//Это устанавливает проверку на минимальную длину записанного
export const minLengthCreator = (minLength) => (value) => {
    return (
        value && value.length < minLength ? `Min length is ${minLength} symbols` : undefined
    )
};

