// //проверка на правильность заполнения МЫЛА
export const email = (zzzz) => (value) => {
    debugger
    return (
        value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)
            ? 'Invalid email address'
            : !zzzz.status ? <span>Пароль или логин не правильный</span> : undefined
    )

};
