import React, {useEffect} from 'react';

import ReduxLoginForm from "./Login";

import './style/style.css';
import {compose} from "redux";
import {connect} from "react-redux";
import redirectInProfiles from "../../hoc/step2redirectInProfiles";
import {Exit, GetUserFromServer, SendUserFormForServer} from "./LoginReducer";
import {getLoginUser, getUserEntry} from "../../user-selector/LoginSelector";
import Cookies from "js-cookie";

let  LoginCont=(props)=>{

    useEffect(()=> {
        props.GetUserFromServer()
    },[]);

    let EntryForm = (formData) => {
        props.SendUserFormForServer(formData)
        Cookies.set('History', 'profile')
    };

    return (
            <div className={'entry_Section'}>
                  <ReduxLoginForm onSubmit={EntryForm}  />
            </div>
    )
};
const mapStateTo = (state) => ({
    user: getLoginUser(state)
    , userEntry: getUserEntry(state)
});
export default compose(connect(mapStateTo, {
    Exit,
    GetUserFromServer,
    SendUserFormForServer
}), redirectInProfiles)(LoginCont)


